import React from 'react';
import pure from 'recompose/pure';

const Cours = () => (
  <span>
    <h2 className="section-title font-alt mb-70 mb-sm-40">Cours de couture</h2>
    <div className="section-text mb-80 mb-xs-60">
        <div className="row">
          <div className="col-sm-3" />
            <div className="col-sm-6">
                <table className="table table-hover">
                  <tbody>
                    <tr>
                        <td>
                            Les 3 h
                            <div className="small">
                                Le samedi - Jusqu'a 4 personnes simultanément
                            </div>
                        </td>
                        <td className="align-right">24€</td>
                    </tr>
                  </tbody>
                </table>
            </div>
            <div className="col-sm-3" />
        </div>
      </div>
  </span>
);

export default pure(Cours);



